/* Botones */
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
:after,
:before {
  box-sizing: inherit;
  padding: 0;
  margin: 0; }

body {
  background: #407ef0;
  font-family: Lato, sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1,
h2,
h3 {
  font-family: Lato, sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

li {
  list-style: none; }

.contenedor {
  width: 90%;
  margin: 0 auto; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px;
  margin: 0 auto; }

.btn-amarillo {
  display: block;
  background: #e08709;
  color: #fff;
  text-decoration: none;
  text-align: center;
  font-size: 2rem;
  padding: 0.5rem 3rem;
  margin: 1rem auto;
  font-weight: 700;
  border: 0.15rem solid #f6a026; }

.btn-amarillo:hover {
  cursor: pointer;
  background: #af6907;
  border: 0.15rem solid #f7ab3e; }

.btn-verde {
  display: inline-block;
  background: #71b100;
  color: #fff;
  text-decoration: none;
  text-align: center;
  font-size: 2rem;
  padding: 0.5rem 3rem;
  margin: 1rem auto;
  font-weight: 700;
  border: 0.15rem solid #92e400; }

.btn-verde:hover {
  cursor: pointer;
  background: #507e00;
  border: 0.15rem solid #a2fe00; }

.btn-contacto {
  display: inline-block;
  background: #e08709;
  color: #fff;
  text-decoration: none;
  text-align: center;
  font-size: 2rem;
  padding: 0.5rem 3rem;
  margin: 1rem auto;
  font-weight: 700;
  border: 0.15rem solid #f6a026; }

.btn-contacto:hover {
  cursor: pointer;
  background: #af6907;
  border: 0.15rem solid #f7ab3e; }

/* Dark Mode */
.dark-mode {
  background: #1a1a1a; }
  .dark-mode p {
    color: #FFFFFF; }
  .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode li, .dark-mode label {
    color: #e1e1e1; }
  .dark-mode .anuncios h2 {
    color: #333333;
    border: 0.1rem solid, #333333; }
  .dark-mode .anuncio {
    background: #333333; }
  .dark-mode .precio {
    color: #71B100; }
  .dark-mode .contenedor-anuncios {
    background: #aeaeae; }
  .dark-mode .contenido-anuncio {
    background: #262626; }
  .dark-mode .iconos-detalles img {
    filter: invert(100%); }
  .dark-mode .scont-nosotros {
    background: #303030; }
  .dark-mode .campo input, .dark-mode .campo textarea, .dark-mode .campo select {
    background: #d4d4d4;
    color: #404040; }

