/* Nosotros */
.nosotros h1, .nosotros h2 {
  font-weight: 900; }

.nosotros .mision {
  margin: 2rem auto;
  border-radius: 2rem;
  box-shadow: 0.5rem 0.5rem 1rem #979696; }

.iconos {
  margin: 2rem auto;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr); }

.icono {
  width: 100%;
  margin: 0 auto; }
  .icono h4 {
    font-size: 700;
    font-size: 2rem;
    text-align: center;
    text-transform: uppercase; }
  .icono p {
    text-align: center;
    font-size: 6rem;
    color: #1155d2;
    font-weight: 300;
    text-shadow: 1rem 1rem 1rem #a0a0a0; }
  @media (min-width: 768px) {
    .icono h4 {
      font-size: 3rem; }
    .icono p {
      font-size: 10rem; } }

/* Contacto */
.contacto {
  margin: 2rem auto 1rem auto; }
  .contacto h1 {
    font-weight: 900;
    margin: 0 auto; }
  .contacto h3 {
    font-weight: 700; }
  .contacto h4 {
    text-align: center;
    margin: 1rem auto 0.5rem auto; }
  @media (min-width: 768px) {
    .contacto .img {
      margin: 0 auto;
      max-width: 70%; } }
  @media (min-width: 768px) {
    .contacto .img2 {
      margin: 0 auto;
      max-width: 50%; } }
  .contacto p {
    font-size: 2rem;
    text-align: center; }
    .contacto p i {
      color: #2ccf2c; }
    .contacto p a {
      font-weight: 700;
      text-decoration: none;
      color: #000000; }
  .contacto ul {
    width: 100%;
    margin: 0 auto; }
  .contacto .telegram i {
    color: #0c61b1;
    font-size: 3rem; }
  .contacto .facebook i {
    color: #0e31cc;
    font-size: 3rem; }

@media (min-width: 768px) {
  .medios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); } }

/* Productos */
.pProductos {
  margin: 2rem auto 1rem auto;
  padding: 0 0 2rem 0;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr); }
  @media (min-width: 768px) {
    .pProductos {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(4, 1fr); } }

.productos.cate {
  background: #b6cef9;
  height: 100%;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr); }
  @media (min-width: 768px) {
    .productos.cate {
      height: auto;
      padding: 3rem 0;
      display: grid;
      gap: 1rem;
      grid-template-columns: repeat(4, 1fr); } }

.catedescripcion {
  color: #0d42a3;
  background: #f2f2f2;
  border-radius: 1rem;
  border: 0.3rem solid #0d42a3;
  box-shadow: 0rem 0rem 2rem #1a1a1a;
  margin: 1rem auto; }
  .catedescripcion h3 {
    margin: 0 auto; }
  .catedescripcion p {
    margin: 0 auto;
    text-align: center;
    color: #0d42a3;
    padding: 0rem 2rem; }

.producto {
  margin: 1rem auto;
  padding: 1rem; }

.categoria {
  height: auto; }

.categoria.cate {
  height: auto; }
