html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
:after,
:before {
    box-sizing: inherit;
    padding: 0;
    margin: 0;
}
body {
    background: lighten($color: $azul, $amount: 25);
    font-family: Lato, sans-serif;
    font-size: 1.6rem;
    line-height: 1.8;
}
p {
    color: #000;
    font-size: 2rem;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
}
a {
    text-decoration: none;
}
img,
picture {
    width: 100%;
    display: block;
}
h1,
h2,
h3 {
    font-family: Lato, sans-serif;
    margin: 2.5rem 0;
    font-weight: 300;
    text-align: center;
}
h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}
li {
    list-style: none;
}
.contenedor {
    width: 90%;
    margin: 0 auto;
}